import "./menu.scss";
import "./../../global.scss"
function Menu({ menu, set }) {
  return (
    <div className={"Menu " + (menu && "active")}>
      <ul>
        <li onClick={()=>{set(false)}}>
          <a href="#Home">Home</a>
        </li>

        <li onClick={()=>{set(false)}}>
          <a href="#Intro">Intro</a>
        </li>

        <li onClick={()=>{set(false)}}>
          <a href="#Projects">Projects</a>
        </li>
        <li onClick={()=>{set(false)}}>
          <a href="#Contact">Contact</a>
        </li>
      </ul>
    </div>
  );
}

export default Menu;
