import "./contact.scss";
import "./../../global.scss"
import ConnectWithoutContactIcon from "@mui/icons-material/ConnectWithoutContact";
import FacebookIcon from '@mui/icons-material/Facebook';
import GitHubIcon from '@mui/icons-material/GitHub';
import {useState} from 'react'
function Contact() {
  const [message,setMessage]= useState(false);
  const handleSubmit = ((e)=>{
    setMessage(true);
    setTimeout(() => {
      setMessage(false);
    }, 3000);
    e.preventDefault();
  });
  return (
    <div className="Contact" id="Contact">
      
      <div className="wrapper">
      <div className="left">
      <div className="center">
      <div className="icon">
      <ConnectWithoutContactIcon></ConnectWithoutContactIcon>
      </div>
      </div>
     <div className="bottom">
     <a href="https://www.facebook.com/karthikeyan.ramachandran.779" target="blank" className="icon">
        <FacebookIcon/>
      </a>
      <a href="https://github.com/Phoenix1192" target= "blank" className="icon">
       <GitHubIcon/>
      </a>
     </div>
      
      </div>
        <div className="right">
          <h2>Contact.</h2>
          <form  onSubmit={handleSubmit}>
          <input type="text" placeholder=" Email" required />
          <textarea placeholder=" Message" required></textarea>
          <button type="submit">Send</button>
          {message && <span> Thanks, I'll reply ASAP :)</span>}
          </form>
        </div>
      </div>
    </div>
  );
}

export default Contact;
